@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:"";content:none}q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}@media screen and (min-width: 769px){.form__result{width:100%}.form__temp-icon{margin:auto}.form__list{width:-webkit-max-content;width:-moz-max-content;width:max-content}}body{font-family:"Bangers", cursive}.app{display:flex;flex-direction:column;align-items:center}.app__background{position:fixed;z-index:-1;opacity:0.8}.app__header{font-size:40px;padding:1rem;letter-spacing:4px;color:yellow}.app__header--md{font-size:20px;text-align:center}.form{color:white;background:black;opacity:0.85;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;min-width:80%;min-height:450px;padding:5% 0}.form__search{margin-bottom:1.2rem}.form__input{cursor:pointer;font-size:20px;height:30px}.form__err{width:90%;display:flex;justify-content:center}.form__result{display:flex;flex-direction:column;padding:3%}.form__header{font-size:25px;padding:10px 0}.form__date{padding:3px 0}.form__temp-icon{display:flex;flex-direction:row;align-items:center;align-content:center;margin-bottom:1.5rem}.form__temp{font-size:28px;padding-right:1rem}.form__icon{display:flex;flex-direction:column;justify-content:center;align-items:center}.form__data{border-bottom:1px solid grey;padding:3px}.form__data>span{margin-left:10px}

